.App {
  font-family: sans-serif;
  text-align: center;
  margin-bottom: 4rem;
}

body {
  background-color: #001630;
}

.card{
  background-color: transparent;
}

.ic {
  margin-bottom: 0;
}

.title {
  border-bottom: 2px solid #000;
  padding-bottom: 8px;
  color: #f4e4c2 !important;
}

.bio {
  background: linear-gradient(to bottom, rgba(245, 246, 252, 0.35), rgba(117, 19, 93, 0.5)), url(/Course-Searcher/static/media/bio.af2a3049.jpg);
  color: #fff;
  border-radius: 8px;
}

.bio:hover {
  background: linear-gradient(to bottom, rgba(245, 246, 252, 0.2), rgba(117, 19, 93, 0.3)), url(/Course-Searcher/static/media/bio.af2a3049.jpg);
  cursor: pointer;
  transition: all 250ms ease-in-out;
  border-radius: 0;
}

